/*
  elm-hot creates an additional div wrapper around the app to make HMR possible.
  This could break styling in development mode if you are using Elm UI.

  More context in the issue:
    https://github.com/halfzebra/create-elm-app/issues/320
*/
[data-elm-hot="true"] {
  height: inherit; }

h1 {
  font-size: 1.4em; }

header {
  font-size: 1.1em;
  margin: 0.5em 0em; }

img {
  margin: 20px 0;
  max-width: 200px; }

div.upper-left {
  position: absolute;
  top: 10px;
  left: 20px; }

div.upper-right {
  position: absolute;
  top: 10px;
  right: 20px; }

div.lower-left {
  position: absolute;
  bottom: 10px;
  left: 20px; }

div.lower-right {
  position: absolute;
  bottom: 10px;
  right: 25px; }

input {
  width: 24em;
  font-size: 1em;
  padding: 0.2em 0.4em; }

input[type='checkbox'] {
  width: 0.8em;
  height: 0.8em;
  margin-right: 0.75em; }

ul {
  padding: 0;
  list-style-type: none; }

li {
  margin: 0.5em 0em; }

.flex-grid {
  display: flex; }

.col {
  flex: 1 1; }
